@media (max-width: 1200px) {
    .container {
        width: 90%;
        overflow: hidden;
    }
}

@media (max-width: 870px) {
    .banner {
        height: calc(100vh - 118px);
        height: calc(var(--vh, 1vh) * 100 - 118px);
    }
    .navigation__link {
        font-size: 25px;
    }
    .header__socials {
        padding-bottom: 10px;
    }
}

@media (max-width: 500px) {
    .navigation__link {
        font-size: 20px;
    }
    .banner {
        height: calc(100vh - 111px);
        height: calc(var(--vh, 1vh) * 100 - 111px);;
    }
}